form[name=get_started] {
	@media all and (min-width: $mediumWidth) {
		margin: 4rem auto 0;
		padding: 0 8rem;
		max-width: $mediumWidth;
	}
	input {
		margin-top: 1rem;
		padding: .5em;
		width: 100%;
		border: 0;
		border-radius: 3px;
		font-size: 2rem;
	}
	input:not([type=submit]) {
		border: 1px solid #999;
	}
	input[type=submit] {
		color: #fff;
		background-color: #4DAFD9;
	}
}