.btn, button {
	padding: .5em 2em;
	font-size: 1.8rem;
	line-height: 1.27;
	font-weight: bold;
	text-decoration: none;
	border: .12em solid;
	border-radius: .23em;
	&:hover, &:active {
		text-decoration: underline;
	}
}
.btn-prev {
	color: $primary;
	background: #fff;
}
.btn-next, .btn-positive {
	color: #fff;
	background: $primary;
	border-color: $primary;
}
.btn-negative {
	color: #fff;
	background: #999;
	border-color: #999;
}

.confirmation-buttons {
	display: flex;
	flex-flow: row wrap;
	padding-top: 3rem;
	.btn, button {
		flex: 1 0 100%;
		margin: 1rem 0;
		@media all and (min-width: $mediumWidth) {
			flex: 1 0 auto;
			margin: 0 2em;
		}
	}
}