@media all and (min-width: $mediumWidth) {
	.page-main section[role=main] > div {
		text-align: left;
	}
}

.test-areas {
	margin: 0;
	padding: 0;
	list-style: none;
	font-size: 2rem;
	> li {
		margin-top: 2em;
		padding-top: 2em;
		@media all and (min-width: $mediumWidth) {
			padding-top: 0;
			padding-left: 4em;
			background-position: top left;
			h2, p {
				margin: .25em 0;
			}
		}
	}
}